@import url(//db.onlinewebfonts.com/c/0b811799540d4b23474c8cd3c9576879?family=FF+Tisa+Sans);
.title-container {
  font-size: 3.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.artemis-logo {
  max-width: 10em;
  margin-right: 20px;
  -webkit-animation: fade-in 8000ms 500ms 1;
          animation: fade-in 8000ms 500ms 1;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.title_text {
  text-align: center;
  max-width: 1000px;
  opacity: 1;
}

.title_subtext {
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-style: italic;
}

.noticeWrapper {
    background-color: white;
    text-align: center;
    color: red;
}

.redText {
    color: red;
}
.demo-container {
  padding: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #c2c1bf;
}

.screenshot {
  max-width: 800px;
  border-radius: 25px;
  box-shadow: 5px 5px 5px rgb(26, 25, 25);
  transition: 500ms ease-in-out;
}

.screenshot:hover {
  width: 1100px;
  max-width: 1100px;
}

.screenshot:hover {
  transform: translateY(-10px) translateX(-10px);
  box-shadow: 15px 15px 15px rgb(17, 16, 16);
}

.flex {
  padding: 4%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.reverse {
  flex-direction: row-reverse;
}

.screenshot-text {
  max-width: 800px;
  padding: 3rem;
  text-align: center;
  font-family: 'FF Tisa Sans';
  font-size: 34px;
};

.team-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 3em;
}

/* 'Meet the Team!' Heading */

.team-container__intro {
  text-align: center;
  margin-bottom: 1.5em;
  font-size: 55px;
}

/* Container to hold team members */

.team-members-container {
  width: 80%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 3em;
  grid-gap: 3em;
  gap: 3em;
}

.logo-container {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
}

/* Team Member Cards */

.team-member {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team-member > * {
  margin-bottom: 0.5em;
}

.team-member__img {
  max-width: 100%;
  width: 8em;
  -webkit-animation: img-shadow 3000ms infinite;
          animation: img-shadow 3000ms infinite;
}

@-webkit-keyframes img-shadow {
  0% {
    -webkit-filter: drop-shadow(0px 0px 2.3px);
            filter: drop-shadow(0px 0px 2.3px);
  }
  50% {
    -webkit-filter: drop-shadow(0px 0px 8px);
            filter: drop-shadow(0px 0px 8px);
  }
  100% {
    -webkit-filter: drop-shadow(0px 0px 2.3px);
            filter: drop-shadow(0px 0px 2.3px);
  }
}

@keyframes img-shadow {
  0% {
    -webkit-filter: drop-shadow(0px 0px 2.3px);
            filter: drop-shadow(0px 0px 2.3px);
  }
  50% {
    -webkit-filter: drop-shadow(0px 0px 8px);
            filter: drop-shadow(0px 0px 8px);
  }
  100% {
    -webkit-filter: drop-shadow(0px 0px 2.3px);
            filter: drop-shadow(0px 0px 2.3px);
  }
}

.footerwrapper {
  padding: 1em;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #c2c1bf;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Source Sans Pro', sans-serif;
  background-color: #6c3eb1;
  justify-content: center;
  align-items: center;
}

.icon {
  transition: 500ms ease-in-out;
}

.icon:hover {
  transform: scale(1.1);
}

/******************************
 *
 * Media Query Breakpoints
 * 
 ******************************/

/* devices smaller than 600px ( Phones ) */
@media (max-width: 599px) {
  .title-container {
    font-size: 2rem;
    flex-direction: column;
  }

  .artemis-logo {
    max-width: 10em;
    margin-bottom: 30px;
  }

  .title_text {
    text-align: center;
    max-width: 400px;
  }

  .mission-container {
    text-align: center;
    font-size: 1rem;
    padding: 0 5em;
  }

  .screenshot {
    max-width: 23em;
  }

  .screenshot:hover {
    width: 375px;
    max-width: 375px;
  }

  .screenshot-text {
    font-size: 1.1em;
    margin-bottom: 1em;
    min-width: 350px;
    padding: 1em;
  }

  .team-member__img {
    width: 6em;
  }
}

